.decor{
    margin-top: 1rem;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

}
input, textarea { 
    outline: none;
    }
    .form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    }
    h1 {
    margin-top: 0;
    font-weight: 500;
    }
    form {
    position: relative;
    width: 80%;
    border-radius: 30px;
    background: #fff;
    }
  
    .form-inner {
        width: 50%;
    padding: 2rem;
    }
    .form-inner input,
    .form-inner textarea {
    display: block;
    width: 100%;
    padding: 15px;
    margin-bottom: 10px;
    border: none;
    border-radius: 20px;
    background-color: lightgrey;
    }
    .form-inner textarea {
    resize: none;
    }
    button {
    width: 40%;
    padding: 10px;
    margin-top: 20px;
    border-radius: 20px;
    border: none;
    background: #777; 
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    }
    button:hover {
    background:grey;
    color: black
    } 
    @media screen and (max-width: 768px) {
    form {
    width: 100%;
    margin-top: 3rem;
    }
    .form-inner {
        width: 100%;
    padding: 0px;
    }
    }