
html, body{height:auto}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
h1{
  color: black;
}

.ret{
  width: 100%;
  height: 100vh;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.left{
  animation-name: leftSlide;
  animation-duration: 3s;
  animation-fill-mode: linear;
  transition: 0.1s ease-in;
  margin-right: 0px;
  
  
}
.right{
  animation-duration: 3s;
  animation-fill-mode: forwards;
  transition: 1s ease-in;
}



@keyframes leftSlide{
0%{
  opacity: 0;
  margin-left: 1000px;
}
50%{
  opacity: 0;
}
75%{
  opacity: 0.5;
}
100%{
  opacity: 1;
  margin-right: 0px;
}
}

.links{
  color: rgb(59, 57, 57);
}
.links:hover{
  color: black;
}

#contact{
  width: 100%;
  height:  90vh;
  padding: 4rem;
 
}
.formContainer{
  padding: 3rem;
  display: grid;
}

.form-submit{
  width: 8rem;
}

